@import 'styles/global/breakpoints.scss';
@import 'styles/global/variables.scss';
@import 'styles/global/buttons.scss';

.button_container {
  display: flex;
  flex: 1;
  flex-direction: column;

  @include breakpoint(medium) {
    flex-direction: row;
  }

  @include breakpoint(large) {
    flex-direction: row;
  }

  @include breakpoint(wide) {
    flex-direction: row;
  }

  .clear_and_reset_button_container {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;

    .clear_button_container {
      display: flex;
      flex: 1;

      .button {
        @include button_block;
      }
    }

    .reset_button_container {
      display: flex;
      flex: 1;
      margin: 0 0 0 $margin_main;

      .button {
        @include button_block;
      }
    }
  }

  .message_container {
    display: flex;
    min-width: 200px;
    min-height: 50px;
    max-height: 50px;
    flex: 1;
    margin: $margin_main 0;
    font-size: $font_size_small;
    background-color: $color_white;
    border: 0;
    outline: none;
    justify-content: center;
    align-items: center;

    @include breakpoint(medium) {
      margin: 0 0 0 $margin_main;
    }
  
    @include breakpoint(large) {
      margin: 0 0 0 $margin_main;
    }
  
    @include breakpoint(wide) {
      margin: 0 0 0 $margin_main;
    }

    .message {
      display: flex;
    }
  }

  .copy_button_container {
    display: flex;

    @include breakpoint(medium) {
      margin: 0 0 0 $margin_main;
    }

    @include breakpoint(large) {
      margin: 0 0 0 $margin_main;
    }
  
    @include breakpoint(wide) {
      margin: 0 0 0 $margin_main;
    }

    .button {
      @include button_long;
    }
  }
}
